"use client"
import { useState } from "react"

export default function Invoices() {
  const [file, setFile] = useState<File | null>(null)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (file) alert(`Invoice "${file.name}" uploaded âœ…`)
    setFile(null)
  }

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Upload Invoice</h1>
      <form
        onSubmit={handleSubmit}
        className="bg-white p-6 rounded-xl shadow-md max-w-md space-y-4"
      >
        <input
          type="file"
          onChange={(e) => setFile(e.target.files?.[0] || null)}
          className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
          aria-label="Invoice file upload"
          required
        />
        <button
          type="submit"
          className="w-full bg-teal-600 hover:bg-teal-700 text-white font-semibold py-2 rounded-lg"
        >
          Upload
        </button>
      </form>
    </div>
  )
}
